@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins", sans-serif;
}

.side-logo {
  margin-bottom: 22px;
  align-content: center;
  max-width: 149px;
  max-height: 89px;
}

.sidebar {
  position: sticky;
  top: 0;
  left: 0;
  bottom: 0;
  max-width: 100%;
  width: 174px;
  height: 100vh;
  min-height: 100%;
  color: #fff;
  overflow: hidden;
  transition: all 0.5s linear;
  background-color: #fff;
  border-right: 1px solid #e7e7e7;
}

/* .sidebar:hover {
  width: 280px;
} */

.space {
  padding: 9px;
}

.menu {
  max-height: 100%;
  position: relative;
  list-style: none;
  padding: 0;
}

.menu li {
  border-radius: 5px;
}

.menu li a:hover,
.active {
  background-color: #1ebbee;
  color: #fff !important;
}

.menu li a:hover span,
.menu li a:hover i {
  color: #fff !important;
}

.active .pie-chart {
  color: #fff;
}

.active-span {
  color: #fff !important;
}

.menu a {
  max-height: 100%;
  max-width: 100%;
  border-radius: inherit;
  color: #737791;
  font-size: 15px;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 9px 10px;
  transition: all 0.3s linear;
  cursor: pointer;
}

.menu a span {
  overflow: hidden;
  color: #737791;
}

.menu a i {
  font-size: 1.2rem;
  color: #737791;
}

.Signout {
  border-top: 10px;
  border-color: black;
  position: absolute;
  top: 500px;
  left: 0;
  width: 100%;
}

.main--content {
  position: relative;
  background: #efefef;
  width: 100%;
  padding-bottom: 10px;
}

.header--wrapper img {
  max-width: 40px;
  max-height: 40px;
  cursor: pointer;
  border-radius: 6px;
  margin-right: 10px;
}

.header--wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  background: #fff;
  padding: 0px 15px 0px 15px;
}

.nameuser {
  font-size: 11px;
}

.namefinance {
  font-size: 13px;
}

.notification {
  display: flex;
  background-color: #fffaf1;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ffa412;
}

.alert {
  color: #ffa412;
  align-items: start;
  padding: 0;
}

.nav-link {
  color: #737791;
}

.nav-underline {
  margin-left: 25px;
}

.card-heading {
  display: flex;
}

.card-main {
  background: #fff;
  padding: 20px;
  border-radius: 10px;
}

.dashboard_cards {
  border-radius: 10px;
  padding: 5px 16px;
}

.card_title {
  font-size: 12px;
}

.card_subtitle {
  font-size: 10px;
  color: #4079ed;
  padding: 0px;
}

.card_count {
  font-size: 18px;
  padding-top: 9px;
}

.received-quot {
  background-color: #fff4de;
}

.img-size {
  max-height: 25px;
  margin-top: 10px;
}

.carduppercontent {
  font-size: 13px;
}

