}

.approved-quot {
  background-color: #dcfce7;
}

.ops {
  background-color: #f3e8ff;
}

.admin-button {
  padding: 0%;
  border: 0;
  height: 0px;
}

.split {
  background-color: #ffffff;
  height: 0px;
  border: 0;
  margin-bottom: 0px;
}

.chat-button {
  background-color: #119856;
  padding: 30px;
