}

.reports {
  background-color: FFFAF1;
  font-size: 30px;
  font-weight: lighter;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 15px;
  padding: 100px;
  border: 1px solid #ffa412;
}

.report-alert {
  color: #ffa412;
}

.report-main {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  border-radius: 10px;
  padding: 50px 20px;
  margin-top: 25px;
  height: 62vh;
}

.btn {
  text-align: start;
}

.main {
  background-color: #119856;
  padding: 50px;
}

.pdadate {
  display: flex;
}

.btn-color {
  background-color: #007ac12d;
  border-color: #007ac1fb;
}

.badge-text {
  color: #007ac1fb;
}

.back {
  background-color: #ffffff;
  padding: 0;
}

.draft {
  border-color: #007ac1;
  color: #007ac1;
  background-color: #007ac11e;
}

.book {
