/* loginpage start */
.transocean_login {
  position: relative;
}

.transocean_login .logo {
  max-height: 150px;
  max-width: 200px;
  align-items: center;
  /* margin-top: 150px;
    margin-left: 300px; */
  margin: 0 auto;
}

.transocean_login .mainpng {
  max-width: 500px;
  max-height: 500px;
  padding: 20px;
}

.logincard {
  margin-top: 20px;
}

.transocean_login .maincard {
  padding: 25px;
  border-radius: 10px;
  border: 1px solid rgba(102, 100, 100, 0.253);
  box-shadow: 0 4px 8px 0 rgba(199, 198, 198, 0.2),
    0 6px 20px 0 rgba(207, 207, 207, 0.19);
}

.transocean_login .login_text {
  text-decoration: underline;
  padding-bottom: 50px;
}

.transocean_login .form-control {
  width: 400px;
  max-width: 100%;
}

.transocean_login .form-check {
  display: flex;
  justify-content: center;
}

.transocean_login .form-check-input {
  margin: 0 5px 0 -25px;
}

.transocean_login .buttons {
  padding-bottom: 30px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  border-radius: 0%;
}

.transocean_login .btn-primary {
  background-color: #007bc1;
  display: flex;
  justify-content: center;
}

.transocean_login .group {
  position: absolute;
  left: 0;
  top: 0;
}

.transocean_login .bottom-head a,
.footerlinks a {
  color: #4d5959 !important;
  text-decoration: none;
}

.bottom-head {
  display: flex;
  justify-content: center;
  /* position: absolute; */
  width: 100%;
  bottom: 0;
  margin: 10px 0px;
  font-size: 13px;
}
.copyright,
.footerlinks {
  margin: 0px;
  /* padding:0px; */
}
.footerlinks.pl-2 {
  padding-left: 15px;
}
.transocean_login .nav-link {
  margin: 0 20px;
  border-radius: 0px;
}

.transocean_login .nav-pills .nav-link.active,
.transocean_login .nav-pills .show > .nav-link {
  background-color: #007bc1;
  color: white;
}

.transocean_login .nav-pills .nav-link {
  border-radius: 0px;
  border: 0.2px solid #c4bfbf;
  color: rgb(0, 0, 0);
  margin: 0px;
}

.header--wrapper .btn-group {
  margin-top: 9px;
}

.dropdown-toggle::after {
  display: inline-block;
  margin-left: 0.255em;
  vertical-align: 6px;
  content: "";
  border-top: 0.3em solid;
  border-right: 0.3em solid transparent;
  border-bottom: 0;
  border-left: 0.3em solid transparent;
}

.transocean_login .forgotpassword {
  font-size: 13px;
}

.menulist {
  font-size: 13px;
}

.fullbody {
  width: calc(100% - 159px);
}

.menugap {
  margin-bottom: 33px;
}

.transocean_login .rememberme {
  font-size: 13px;
}

.same-level {
  max-height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.signoutmain {
  padding-top: 10px;
}

.button-row {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.usersep {
  border-bottom: 1px solid rgba(151, 151, 151, 0.313);
  padding-bottom: 12px;
}
.version {
  font-size: 10px;
}
.loginpageversion {
  font-size: 12px;
  margin-bottom: 20px;
}
.alignbox {
  display: flex;
  align-items: center;
  min-height: 90vh;
  padding: 45px 101px;
}

/* loginpage end */

@media only screen and (max-width: 1243px) {
  .transocean_login .mainpng {
    display: none !important;
  }

  .notification h6 {
    font-size: 13px;
  }

  .notification.new {
    /* font-size: 1px; */
  }

  .transocean_login .maincard {
    padding: 25px;
    max-width: 80%;
    margin: 0 auto;
    background-color: #fff;
  }

  .transocean_login .group {
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0;
  }

  .dashboard_cards {
    border-radius: 10px;
    padding: 5px 16px;
  }

  .same-level {
    height: auto;
  }

  /* .bottom-head {
            flex-direction: row;
            font-size: 13px;
        } */

  .transocean_login .logo {
    height: 136px;
    width: 200px;
    margin-top: 100px;
  }

  .font {
    font-size: 13px;
  }

  .transocean_login .nav-pills .nav-link {
    border-radius: 0px;
    border: 0.2px solid #c4bfbf;
    color: rgb(0, 0, 0);
    margin: 0px;
    padding: 8px 15px;
  }

  .transocean_login .buttons {
    padding-bottom: 10;
  }

  .transocean_login .login_text {
    text-decoration: underline;
    padding-bottom: 40px;
  }

  .transocean_login .bottom-head a,
  .footerlinks a {
    color: #4d5959 !important;
    text-decoration: none;
  }

  .header--wrapper .btn-group {
    margin-top: 10px;
  }

  .header--wrapper .downarrow {
    margin-top: 10px;
  }

  .Signout {
    border-top: 10px;
    border-color: black;
    position: absolute;
    top: 650px;
    left: 0;
    width: 100%;
  }
}

.loader {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999; /* Ensures it appears above other content */
}
